.nav-our-works {
    display: flex;
    justify-content: center;
    gap: 50px;
    align-items: center;
    font-size: 14px;
    line-height: 17px;
    padding-top: 50px;
}
.nav-our-works a {
    color: #008bf5;
    text-decoration: none;
}
.process {
    width: 100%;
    height: 100vh;
}
.h2_background_color_flex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.h2_background_color {
    width: 700px;
    height: 100px;
    text-align: center;
    background: #008bf5;
    line-height: 100px;
    border-radius: 100px;
    margin: 50px 0;

}
.h2_background_color h2 {
    color: white;
    padding: 0;
}

.process_general_flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 50px;
}


